import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators} from '@angular/forms';
import * as $ from 'jquery';

@Component({
  selector: 'app-first-question',
  templateUrl: './first-question.component.html',
  styleUrls: ['./first-question.component.css']
})
export class FirstQuestionComponent implements OnInit {

  constructor() { }

  reply = '';
  replyArray = 'Đúng vậy ^^ Em yêu anh nhất vũ trụ luôn đó'.split('');
  i = 0;
  gui = "GỬI"
  ngOnInit() {
    $(document).ready(function(){
    $('.btn-no').hover(function(){
      var x = Math.random()*80;
      var y = Math.random()*80;
      var top = x + '%';
      var left = y + '%';
      $('.btn-no').css({ "top": top, "left": left });
      });
      $('.btn-send').hover(function(){
	$('.btn-send').text("Gửi đi mà^^");
      });
      $('.btn-send').mouseleave(function(){
      	$('.btn-send').text("GỬI");
      })
    });
    }
	
    autoComplete(){
    if (this.i < this.replyArray.length){
    	this.reply += this.replyArray[this.i];
	this.i++;
	}else{
	this.reply = '';
	this.i = 0;
	}
    }

    onSend(){
    	$('.result').css("display", "block");
	$('.btn-send').text("Đáng yêu lắm^^");
	$('.btn-send').attr("disabled", true);

    }

}
